.main-content {
    height: 100%;
}

.content {
    min-height: 500px;
}

.side-status {
    margin-left: 16px;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.slider-text {
    line-break: anywhere;
    animation: slider 0.25s ease-in;
}

@keyframes slider {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@media (max-width: 619px) {
    .main-box {
        flex-wrap: wrap;
    }

    .content {
        --grid: 12 !important;
    }

    .side-status {
        --grid: 12 !important;
        margin-left: 0;
        margin-top: 16px;
    }
}
